import { request } from "./request";

// 
export function getAlbumComment(queryInfo) {
    return request({
        url: '/comment/album',
        params: queryInfo
    })
}

// 
export function getPlayComment(queryInfo) {
    return request({
        url: '/comment/playlist',
        params: queryInfo
    })
}

// 
export function getSongComment(queryInfo) {
    return request({
        url: '/comment/music',
        params: queryInfo
    })
}