import { request } from "./request";

// 
export function getSinger() {
  return request({
    url: '',
  })
}

// 
export function getNewSong(info) {
  return request({
    url: '/album/new',
    params: info
  })
}

// 
export function getNewSongInfo(id) {
  return request({
    url: '/album',
    params: { id }
  })
}

// 
export function getAlbumDet(value) {
  return request({
    url: `/artist/desc`,
    params: {
      id: value
    }
  })
}

// 
export function getSimiSinger(value) {
  return request({
    url: `/simi/artist`,
    params: {
      id: value
    }
  })
}

//  
export function getPlaylistDet(value) {
  return request({
    url: `/playlist/detail`,
    params: {
      id: value
    }
  })
}

// 
export function getPlaylistCat() {
  return request({
    url: `/playlist/catlist`,
  })
}

// 
export function getPlaylistTop(queryInfo) {
  return request({
    url: `/top/playlist`,
    params: queryInfo
  })
}

// 
export function getSongsDet(ids) {
  return request({
    url: `/song/detail`,
    params: {
      ids
    }
  })
}

// 
export function getLyric(id) {
  return request({
    url: `/lyric`,
    params: {
      id
    }
  })
}

// 
export function getSimiPlaylist(id) {
  return request({
    url: `/simi/playlist`,
    params: {
      id
    }
  })
}

// 
export function getSimiSongs(id) {
  return request({
    url: `/simi/song`,
    params: {
      id
    }
  })
}


//  url
export function getSongUrl(id) {
  return request({
    url: '/song/url',
    params: { 
      id
    }
  })
}

